.formInput {
  display: flex;
  flex-direction: column;
  width: 280px;
}

input {
  padding: 15px;
  margin: 6px 0px;
  border-radius: 5px;
  border: 1px solid grey;
}

label {
  font-size: 12px;
  color: gray;
  margin-top: 20px;
}

span {
  font-size: 12px;
  padding: 3px;
  color: red;
  display: none;
}

input:invalid[focused="true"] ~ span {
  display: block;
}
